import React from 'react';
import { Card, Col, Row } from 'react-bootstrap';
import { Listing } from '../models/models';

export interface IListingCardProps {
  Listing: Listing;
}

export interface IListingCardState {

}

export default class ListingCard extends React.Component<IListingCardProps, IListingCardState> {

  constructor(props: IListingCardProps) {
    super(props);
    this.state = {};
  }

  public render(): React.ReactElement<IListingCardProps> {
    return (
      <Col xs={12} md={6} lg={6}>
        <Card className="listingCard">
          <Card.Header><span className="listingName">{this.props.Listing.Title}</span></Card.Header>
          <Row>
            {/* <Col xs={3} md={4} lg={4}>
              <Card.Img variant="top" src={this.props.Listing.Image} className="ListingImage" />
            </Col> */}
            {/* <Col  xs={8} md={8} lg={8}> */}
            <Col>
              <Card.Body>
                <Row>
                  <Col xs={9} md={9} lg={9}>
                    <Card.Text className="listingIntro">
                      <span dangerouslySetInnerHTML={{ __html: this.props.Listing.avCustomAnbudsbeskrivelse }} />
                    </Card.Text>
                  </Col>
                  {/* <Col xs={3} md={3} lg={3}>
                    <Card.Img variant="top" src={this.props.Listing.Image} className="ListingImage" />
                  </Col> */}
                </Row>
                {/* <Row>
                  <Col>
                  <Card.Text className="ListingDescription">
                    <span dangerouslySetInnerHTML={{ __html: this.props.Listing.Description }} />
                  </Card.Text>
                  </Col>
                </Row> */}
              </Card.Body>
            </Col>
          </Row>
          <Card.Footer><div style={{float:"left"}}>{this.props.Listing.avCustomFag}</div></Card.Footer>
        </Card>
      </Col>
    );
  }

}